import React from "react";
import FadeIn from "react-fade-in";
import {Link, withRouter} from 'react-router-dom';
import {Collapse} from 'react-bootstrap';
import firebase from '../config';

class SidebarPage extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            userRole: '',
            loading: false,
            orderStatusList: [],
            usePermList: []
        };
    }
   

    render() {

        return (

            <div>

                <div className="left-sidebar-pro">
                    <nav id="sidebar" className="">
                        <div className="sidebar-header">
                            <Link to='/Dashboard'>
                                <img className="im_logo" src="/img/logo/logo.png"/></Link>
                         
                        </div>

                        <div className="left-custom-menu-adp-wrap comment-scrollbar">
                            <nav className="sidebar-nav left-sidebar-menu-pro">

                                <ul
                                    className="nav metismenu"
                                    style={{
                                    height: "543px",
                                    overflowY: "auto"
                                }}
                                    id="style-2">
                                    <li
                                                className={this.isPathActive('/Dashboard') || this.state.componentMenuOpen
                                                ? 'active'
                                                : null}>
                                                <Link to="/Dashboard">
                                                    <i className="fa fa-tachometer" aria-hidden="true"></i>
                                                    <span className="mini-click-non space">
                                                        Dashboard</span>
                                                </Link>
                                            </li>
                                     

                                   
                                            {sessionStorage.getItem("userRole") == "Admin"?  
                                     <li
                                                className={this.isPathActive('/EmployeeList') || this.state.componentMenuOpen
                                                ? 'active'
                                                : null}>
                                                <Link to="/EmployeeList">
                                                    <i className="fa fa-tag" aria-hidden="true"></i>
                                                    <span className="mini-click-non space">
                                                        Employees</span>
                                                </Link>
                                            </li>:''}
                                        
                                  
                                            {sessionStorage.getItem("fileRole") == "read&write" || sessionStorage.getItem("fileRole") == "read"?
                                     <li
                                                className={this.isPathActive('/FileList') || this.state.componentMenuOpen
                                                ? 'active'
                                                : null}>
                                                <Link to="/FileList">
                                                    <i className="fa fa-file-text"  aria-hidden="true"></i>
                                                    <span className="mini-click-non space">
                                                        Files</span>
                                                </Link>
                                            </li>:''}
                                            {sessionStorage.getItem("locationRole") == "read&write" || sessionStorage.getItem("locationRole") == "read"?
                                            <li
                                                className={this.isPathActive('/Locations') || this.state.componentMenuOpen
                                                ? 'active'
                                                : null}>
                                                <Link to="/Locations">
                                                    <i className="fa fa-file-text"  aria-hidden="true"></i>
                                                    <span className="mini-click-non space">
                                                        Locations</span>
                                                </Link>
                                            </li>:''}
                                            {sessionStorage.getItem("salesTrackerRole") == "read&write" || sessionStorage.getItem("salesTrackerRole") == "read"?
                                            <li
                                                className={this.isPathActive('/SalesTracker') || this.state.componentMenuOpen
                                                ? 'active'
                                                : null}>
                                                <Link to="/SalesTracker">
                                                    <i className="fa fa-file-text"  aria-hidden="true"></i>
                                                    <span className="mini-click-non space">
                                                        Sales Tracker</span>
                                                </Link>
                                            </li>:''}
                                       
                                       
                                   
                                    <li
                                                className={this.isPathActive('/GSTListPage') || this.state.tableMenuOpen
                                                ? 'active'
                                                : null}>
                                                <a
                                                    className="has-arrow"
                                                    onClick={() => this.setState({
                                                    tableMenuOpen: !this.state.tableMenuOpen
                                                })}
                                                    data-toggle="collapse">
                                                    <i className="fa fa-cog" aria-hidden="true"></i>
                                                    <span className="mini-click-non space">
                                                        Settings</span>
                                                </a>
                                                <Collapse in={this.state.tableMenuOpen}>
                                                    <div>
                                                        <ul className="nav submenu-angl">
                                                            <li>
                                                                <Link to='/City'>
                                                                    City</Link>
                                                            </li>
                                                            <li>
                                                                <Link to='/Departments'>
                                                                    Department</Link>
                                                            </li>
                                                            <li>
                                                                <Link to='/DesignationList'>
                                                                    Designation</Link>
                                                            </li>
                                                           

                                                        </ul>
                                                    </div>
                                                </Collapse>
                                            </li>
                                       
                                   
                                   
                                </ul>

                            </nav>
                        </div>

                    </nav>
                </div>
            </div>
        );
    }

    isPathActive(path) {
        return this
            .props
            .location
            .pathname
            .startsWith(path);
    }
}

export default withRouter(SidebarPage);
