import React from "react";
import {Link} from "react-router-dom";

import {Form} from 'reactstrap';
import HeaderPage from "./header";
import FooterPage from "./footer";
import SidebarPage from "./sidebar";
import firebase from '../config';
import ReactQuill from "react-quill";
import 'react-quill/dist/quill.snow.css';
import SimpleReactValidator from "simple-react-validator";

class MyProfile extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            fullName: '',
            email: '',
            username: '',
            password: '',
            status: '',
            date: new Date().toLocaleString(),
            theme: 'snow',
            employer_sevice_message: "",
            mobile_message:'',

        }
        this.onChange = this
            .onChange
            .bind(this);
            this.validator = new SimpleReactValidator({
              className: "text-danger",
              validators: {
                  passwordvalid: {
                      message: "The :attribute must be at least 6 and at most 30 with 1 numeric,1 special charac" +
                              "ter and 1 alphabet.",
                      rule: function (val, params, validator) {
                          // return validator.helpers.testRegex(val,/^[a-zA-Z0-9]{6,30}$/i) &&
                          // params.indexOf(val) === -1
                          return (validator.helpers.testRegex(val, /^(?=.*\d)(?=.*[!@#$%^&*])(?=.*[a-z]).{6,30}$/i) && params.indexOf(val) === -1);
                      }
                  },
                  passwordMismatch: {
                      message: "confirm password must match with password field.",
                      rule: function (val, params, validator) {
                          return document
                              .getElementById("password_input")
                              .value === val
                              ? true
                              : false;
                      }
                  },
                  whitespace: {
                      message: "The :attribute not allowed first whitespace   characters.",
                      rule: function (val, params, validator) {
                          // return validator.helpers.testRegex(val,/^[a-zA-Z0-9]{6,30}$/i) &&
                          // params.indexOf(val) === -1
                          return (validator.helpers.testRegex(val, /[^\s\\]/) && params.indexOf(val) === -1);
                      }
                  },
                  specialChar: {
                      message: "The :attribute not allowed special   characters.",
                      rule: function (val, params, validator) {
                          // return validator.helpers.testRegex(val,/^[a-zA-Z0-9]{6,30}$/i) &&
                          // params.indexOf(val) === -1
                          return (validator.helpers.testRegex(val, /^[ A-Za-z0-9_@./#&+-]*$/i) && params.indexOf(val) === -1);
                      }
                  },
                  specialCharText: {
                      message: "The :attribute may only contain letters, dot and spaces.",
                      rule: function (val, params, validator) {
                          // return validator.helpers.testRegex(val,/^[a-zA-Z0-9]{6,30}$/i) &&
                          // params.indexOf(val) === -1
                          return (validator.helpers.testRegex(val, /^[ A-Za-z_@./#&+-]*$/i) && params.indexOf(val) === -1);
                      }
                  },
  
                  zip: {
                      message: "Invalid Pin Code",
                      rule: function (val, params, validator) {
                          // return validator.helpers.testRegex(val,/^[a-zA-Z0-9]{6,30}$/i) &&
                          // params.indexOf(val) === -1
                          return (validator.helpers.testRegex(val, /^(\d{5}(\d{4})?)?$/i) && params.indexOf(val) === -1);
                      }
                  },
                  website: {
                      message: "The Url should be example.com ",
                      rule: function (val, params, validator) {
                          // return validator.helpers.testRegex(val,/^[a-zA-Z0-9]{6,30}$/i) &&
                          // params.indexOf(val) === -1
                          return (validator.helpers.testRegex(val, /(http(s)?:\/\/.)?(www\.)?[-a-zA-Z0-9@:%._\+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_\+.~#?&//=]*)/g) && params.indexOf(val) === -1);
                      }
                  },
                  Fax: {
                      message: "Invalid fax number ",
                      rule: function (val, params, validator) {
                          return (validator.helpers.testRegex(val, /^([\w-\.]+)@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.)|(([\w-]+\.)+))([a-zA-Z]{2,4}|[0-9]{1,3})(\]?)$/i) && params.indexOf(val) === -1);
                      }
                  }
              }
          });
      }
  
    
    componentDidMount() {
        var sessionId = sessionStorage.getItem("RoleId");

       

        firebase
            .database().ref('admin_users/' + sessionId).on('value', snapshot => {
         var gstData = snapshot.val();
        
       console.log(gstData);
        this.setState({
            
            fullName: gstData.fullName,
            email: gstData.email,
            username: gstData.username,
            password: gstData.password,
            status: gstData.status,
            contact_number: gstData.contact_number,
            createdAt: gstData.created_on,
            loading: false

            
          });
         
         
        });

    }
    onChange = (event) => {

        this.setState({
            [event.target.name]: event.target.value
        });
    };

    handleSubmit = (event) => {
        var sessionId = sessionStorage.getItem("userId");
      
            event.preventDefault();
            if (this.validator.allValid()) {
                var user = null;
                var ref = firebase
                .database()
                .ref('admin_users/')
                .orderByChild("contact_number")
                .equalTo(this.state.contact_number);
            ref.on('value', snapshot => {
                var user_exist = snapshot.numChildren();
                console.log(user_exist);
    
                if (user_exist > 0) {
                    this.setState({mobile_message: "Mobile number alredy exist", contact_number: ''});
                } else {
    
            let dbCon = firebase
                .database()
                .ref(`/admin_users/${sessionId}`);
            console.log(dbCon);
            dbCon.update({
                fullName: this.state.fullName,
                email: this.state.email,
                username: this.state.username,
                password: this.state.password,
                status: this.state.status,
                contact_number: this.state.contact_number,
                created_on: this.state.date
            });
            this.setState({});
            this
                .props
                .history
                .push("/Dashboard");
        }
    })
} else {
    this
        .validator
        .showMessages();
    this.forceUpdate();
}
    };

    render() {

        const divStyle = {
            'height': '300px'
        };

        return (
            <div>
                <SidebarPage/>

                <div className="all-content-wrapper">
                    <HeaderPage/>

                    <div className="breadcome-area">
                        <div className="container-fluid">
                            <div className="row">
                                <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                                    <div className="breadcome-list">
                                        <div className="row">
                                            <div className="col-lg-6 col-md-6 col-sm-6 col-xs-6">
                                                <div className="breadcomb-wp">

                                                    <div className="breadcomb-ctn">
                                                        <h2>My Profile</h2>
                                                    </div>
                                                </div>
                                            </div>

                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="single-product-tab-area mg-b-30">

                        <div className="single-pro-review-area">
                            <div className="container-fluid">
                                <div className="row">
                                    <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                                        <div className="review-tab-pro-inner">

                                            <h4>My Profile</h4>

                                            <div className="product-tab-list tab-pane fade active in">
                                                <form className="form-horizontal" onSubmit={this.handleSubmit} role="form">
                                                    <div className="box-body">
                                                        <div className="form-group">
                                                            <label htmlFor="inputEmail3" className="col-sm-2 control-label">Full Name</label>
                                                            <div className="col-sm-10">
                                                                <input
                                                                    type="text"
                                                                    className="form-control"
                                                                    placeholder="Full Name"
                                                                    id="fullName"
                                                                    name="fullName"
                                                                    onChange={this.onChange}
                                                                    value={this.state.fullName}/> {this
                                                                    .validator
                                                                    .message("Full Name", this.state.fullName, "required|whitespace|min:2|max:70")}

                                                            </div>
                                                        </div>
                                                        <div className="form-group">
                                                            <label htmlFor="inputEmail3" className="col-sm-2 control-label">Email</label>
                                                            <div className="col-sm-10">
                                                                <input
                                                                    type="email"
                                                                    name="email"
                                                                    id="email"
                                                                    onChange={this.onChange}
                                                                    value={this.state.email}
                                                                    className="required form-control" readOnly/> {this
                                                                    .validator
                                                                    .message("Email", this.state.email, "required|email|min:6|max:70")}
                                                                <div className="text-danger">
                                                                    {" "}
                                                                    {this.state.employer_sevice_message}
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div className="form-group">
                                                            <label htmlFor="inputEmail3" className="col-sm-2 control-label">username</label>
                                                            <div className="col-sm-10">
                                                                <input
                                                                    type="text"
                                                                    name="username"
                                                                    id="username"
                                                                    onChange={this.onChange}
                                                                    value={this.state.username}
                                                                    className="required form-control"/> {this
                                                                    .validator
                                                                    .message("User Name", this.state.username, "required|whitespace|min:2|max:70")}

                                                            </div>
                                                        </div>
                                                        <div className="form-group">
                                                            <label htmlFor="inputEmail3" className="col-sm-2 control-label">Password</label>
                                                            <div className="col-sm-10">
                                                                <input
                                                                    type="text"
                                                                    name="password"
                                                                    id="password"
                                                                    onChange={this.onChange}
                                                                    value={this.state.password}
                                                                    className="required form-control"
                                                                    id="password_input"/> {this
                                                                    .validator
                                                                    .message("Password", this.state.password, "required|passwordvalid|min:6|max:30")}

                                                            </div>
                                                        </div>
                                                        <div className="form-group">
                                                            <label htmlFor="inputEmail3" className="col-sm-2 control-label">Contact Number</label>
                                                            <div className="col-sm-10">
                                                            <input
                                                type="text"
                                                className="form-control"
                                                placeholder="Contact Number"
                                                name="contact_number"
                                                onChange={this.onChange}
                                                value={this.state.contact_number} readOnly/> {this
                                                .validator
                                                .message("Contact Number", this.state.contact_number, "required|numeric|min:4|max:15")}
                                            <div className="text-danger">
                                                {" "}
                                                {this.state.mobile_message}
                                            </div>
                                                            </div>
                                                        </div>

                                                        {/* <div className="form-group">
                                                            <label htmlFor="inputEmail3" className="col-sm-2 control-label">status</label>
                                                            <div className="col-sm-10">
                                                                <label className="radio-inline no-padd">
                                                                    <input
                                                                        type="radio"
                                                                        name="status"
                                                                        value="1"
                                                                        onChange={this.onChange}
                                                                        checked={this.state.usageType === '1'}
                                                                        className="icheck"/>
                                                                    Active &nbsp;&nbsp;</label>
                                                                <label className="radio-inline no-padd">
                                                                    <input
                                                                        type="radio"
                                                                        name="status"
                                                                        value="0"
                                                                        onChange={this.onChange}
                                                                        checked={this.state.usageType === '0'}
                                                                        className="icheck"/>
                                                                    Active</label>
                                                                <span
                                                                    style={{
                                                                    color: 'red'
                                                                }}/>
                                                            </div>
                                                        </div> */}

                                                    </div>
                                                    <div className="row">
                                                        <div className="col-lg-offset-2 col-lg-6 col-md-6 col-sm-12 col-xs-12">
                                                            <div className="text-left custom-pro-edt-ds">
                                                                <button type="submit" className="btn btn-ctl-bt waves-effect waves-light">Save
                                                                </button>
                                                                <Link to="/Dashboard">
                                                                    <button type="button" className="btn btn-ctl-bt waves-effect waves-light">Cancel
                                                                    </button>
                                                                </Link>

                                                            </div>
                                                        </div>
                                                    </div>
                                                </form>

                                            </div>

                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <FooterPage/>

            </div>
        );
    }
}
export default MyProfile;
