    componentDidMount() {
        const {fileID} = this.props.match.params;

        var ref = firebase
            .database()
            .ref(`Files/${fileID}`);

        ref.on('value', snapshot => {
            // var categories = snapshot.val();
            
            // console.log(categories)

            this.setState({avatarURL: snapshot.val().file, 
                name: snapshot.val().name,
                permission_type:snapshot.val().permission_type,
                description:snapshot.val().description,
                created_on: snapshot.val().created_on,
                fileName: snapshot.val().fileName,

                loading: false});
            console.log(this.state.Files);
        });




       
            var ref = firebase
                .database()
                .ref('Employees/');
            ref.once("value", snapshot => {
                const data = [];
                console.log(snapshot.val());
                snapshot.forEach(element => {
                    const usersData = {
    
                        value: element
                            .val()
                            .email,
                        label: element
                            .val()
                            .first_name + " " + element
                            .val()
                            .last_name + "(" + element
                            .val()
                            .email + ")"
                    }
                    data.unshift(usersData);
                });
    
                this.setState({
                    EmployeesList: data
                }, () => {
                    console.log(this.state.EmployeesList, 'Employees');
    
                });
    
            })
        
    
    }
